import React, { useEffect, useState } from 'react';
import {NavLink} from "react-router-dom";
//component
import './../../assets/style.css';
import Modal from './../Modal/Modal';
//icon
import {AiOutlineLeft,AiOutlineRight, AiOutlineRobot, AiOutlineHistory, AiOutlineSetting} from 'react-icons/ai'
import {FaEquals,FaMapMarkerAlt, FaParking} from 'react-icons/fa'
import {MdErrorOutline} from 'react-icons/md'
import {RiWindow2Fill, RiLogoutBoxRLine, RiLoginBoxLine} from 'react-icons/ri';

function SideNavPresenter({isAuthorized}) {
    const menus = [
        { name: "서빙 모드", path: "/map", iconClassName: "bi bi-speedometer2"},
        { name: "로봇 호출", path: "/call", iconClassName: "FaRobot" },
        { name: "픽업대 상황", path: "/pickup", iconClassName: "bi bi-speedometer2" },
        { name: "기록", path: "/history", iconClassName: "bi bi-speedometer2" },
        { name: "에러기록", path: "/error", iconClassName: "bi bi-speedometer2" },
        { name: "설정", path: "/setting", iconClassName: "bi bi-speedometer2" }

    ];
    const [isFullNav, setIsFullNav] = useState(true);
    const [isModalOpen, setIsModalOpen] = useState(false);

    const dbLogoSrc = sessionStorage.getItem('logoSrc');
    const logo_src = '/assets/store_logo/' + dbLogoSrc + '.png';
    const modalTitle = "로그아웃 안내";
    const modalHighlight = "";
    const modalContent = "로그아웃 하시겠습니까?";
    // const logo_src = '/assets/store_logo/the_people_burger/the_people_burger_logo.png'
// 로그아웃 함수
function handleLogoutClick() {   // 로봇 복귀 버튼
    console.log("logout modal click!");
    setIsModalOpen(true);
}
function clickLogoutPermit(){
    window.sessionStorage.removeItem('userId');
    window.sessionStorage.removeItem('mapId');
    window.sessionStorage.removeItem('userName');
    window.sessionStorage.removeItem('logoSrc');
    document.location.href = '/login'
}
    return (
        <div id="side-nav" className={"side-nav " + (isFullNav ? "big-nav" : "small-nav center-align")}>
            
            <div className="center-align" id="top">
            <div onClick={() => setIsFullNav(!isFullNav)} className="toggle-menu-btn right-align">
                    {isFullNav ? (
                        <AiOutlineLeft className="icon icon-1"/>
                    ) : (
                        <AiOutlineRight className="icon icon-1"/>
                    )}
                </div>
                {isAuthorized ?
                    <NavLink to='/my-page' id="nav-user" >
                        {/* <img src={require('/assets/store_logo/the_people_burger/the_people_burger_logo.png').default} alt="logo" /> */}
                        <div className="user-div">
                        <img id="nav-user-logo" src={logo_src} alt="user-logo" />
                        {/* userid로 user img url 불러오기 */}
                        <p>{sessionStorage.getItem('userName')} 매장</p>
                        </div>
                    </NavLink>
                    :
                        <div className=" user-div">
                        </div>
                }
            </div>
            
            <div className="side-class text-color-2">Main Menu</div>
            <NavLink to="/serving" className="nav-item" activeClassName="nav-active">
                <p className=" text-color-1">서빙 모드</p>
                <FaMapMarkerAlt className="icon icon-1"/>

            </NavLink>
            <NavLink to="/monitoring" className="nav-item" activeClassName="nav-active">
                <p className=" text-color-1">모니터링</p>
                <RiWindow2Fill className="icon icon-1"/>

            </NavLink>
            <NavLink to="/history" className="nav-item" activeClassName="nav-active">
                <p className=" text-color-1">기록</p>
                <AiOutlineHistory className="icon icon-1"/>
            </NavLink>
            <NavLink to="/call" className="nav-item" activeClassName="nav-active">
                <p className=" text-color-1">로봇 호출</p>
                <AiOutlineRobot className="icon icon-1" />
            </NavLink>
            <div className="side-class text-color-2">General</div>

            <NavLink to="/setting" className="nav-item" activeClassName="nav-active">
                <p className=" text-color-1"> 설정</p>
                <AiOutlineSetting className="icon icon-1"/>
            </NavLink>
            <NavLink to="/pickup" className="nav-item" activeClassName="nav-active">
                <p className=" text-color-1">픽업대 상황</p>
                <FaParking className="icon icon-1"/>
            </NavLink>
            
            <NavLink to="/error" className="nav-item" activeClassName="nav-active">
                <p className=" text-color-1">에러</p>
                <MdErrorOutline className="icon icon-1"/>
            </NavLink>

            <div className="center-align" id="bottom">
                
                {isAuthorized ?
                    <button id="nav-login" className="round-button background-color-w" onClick={handleLogoutClick}> 
                    <p className="text-color-1"> 로그아웃</p>
                    <RiLogoutBoxRLine className="icon icon-1"/>
                    </button>
                
                    :
                    <NavLink to='/login' id="nav-login" className="round-button">
                        <p className="text-color-1">로그인</p>
                        <RiLoginBoxLine className="icon icon-1"/>

                    </NavLink>
                }
            </div>
            <Modal title={modalTitle} highlight={modalHighlight} content={modalContent} open={isModalOpen} onCancel={()=>setIsModalOpen(false)}
                    onPermit={clickLogoutPermit}>
                </Modal>
        </div>
    );
}

export default SideNavPresenter;
