import React, { Component } from 'react'
import RobotServing from './RobotServing';
import RobotPosition from './RobotPosition';
import TablePosition from './TablePosition';
import Modal from './../Modal/Modal';
// import {getDestinationRobot, getHomeRobot, getAllHomeRobot} from '../Call/CallController';
import {getMapTable, getInitOneRobot, getDestinationRobot, getHomeRobot} from './MapController';
// 아이콘   AiOutlineLock
import {GrMap} from 'react-icons/gr'
import {MdErrorOutline} from 'react-icons/md'

export class MapPresenter extends Component {
    constructor(props) {
        super(props)
        // this.mapContainer = React.createRef()
        this.state = {
            mapTable:[], // componenteDidMount에서 저장
            robotStatus:[], //RobotPosition에서 1초마다 저장
            clickedTableId:0,   //table 클릭 시 저장
            clickedRobotId:0,   //로봇 클릭시 저장
            isModalOpen:false,  // 모달 열림 여부 저장
            modalTitle:"제목",  // 모달에 들어갈 정보 
            modalContent: "내용",   //          저장
        };
    }
    async componentDidMount() {

        let [error, result] = await getMapTable();  //table 위치 불러오기. 한번만 불러옴
        if (error) {
            alert("[MapPresenter] getMapTable 불러오기 실패 : ", error);
            return;
        }
        this.setState(prevState => ({
            ...prevState,
            mapTable: result
        }))
    }

    setRobotStatus(props){
        this.setState(prevState => ({
            ...prevState,
            robotStatus: props
        }))
    }
    

    handelTableClick(tableInfo){
        const  tableId = tableInfo[0];
        const  tableName = tableInfo[1];
        this.setState(prevState => ({
            ...prevState,
            isModalOpen: "table",
            clickedTableId: tableId,
            modalTitle: "로봇 보내기",
            modalContent: tableName+"번 테이블로 로봇을 보내겠습니까?",
            clickedRobotId: 0
        }))
        // nothing do
        console.log("table modal click! "+tableId);
    }
    
    handelRobotClick(i){
        this.setState(prevState => ({
            ...prevState,
            clickedRobotId: i,
        }))
        // nothing do
        console.log("robot click! "+i);
    }
    handelNotRobotClick=()=>{
        this.setState(prevState => ({
            ...prevState,
            clickedRobotId: 0,
        }))
    }
    handleInitClick = () => {   // 로봇 초기화 버튼
        const clickedRobotId = this.state.clickedRobotId;
        if (clickedRobotId == 0){   // 로봇이 선택되지 않았을 때
            this.setState(prevState => ({
                ...prevState,
                isModalOpen: "message",
                modalTitle: "로봇 초기화",
                modalContent: "선택된 로봇이 존재하지 않습니다."
            }))
            return;
        }
        // 선택된 로봇이 존재 할 때
        this.setState(prevState => ({
            ...prevState,
            isModalOpen: "init",
            modalTitle: "로봇 초기화",
            modalContent: clickedRobotId+"번 로봇을 초기화하시겠습니까?"
        }))
        // nothing do
        // console.log("init one modal click!");
    }
    handleReturnClick = () => {   // 로봇 복귀 버튼
        const clickedRobotId = this.state.clickedRobotId;
        if (clickedRobotId == 0){   // 로봇이 선택되지 않았을 때
            this.setState(prevState => ({
                ...prevState,
                isModalOpen: "message",
                modalTitle: "로봇 복귀",
                modalContent: "선택된 로봇이 존재하지 않습니다."
            }))
            return;
        }
        // 선택된 로봇이 존재 할 때
        this.setState(prevState => ({
            ...prevState,
            isModalOpen: "return",
            modalTitle: "로봇 복귀",
            modalContent: clickedRobotId+"번 로봇을 복귀시키겠습니까?"
        }))
        // nothing do
        // console.log("init one modal click!");
    }
    async clickTableModalPermit(userId){
        const destinationTable = this.state.clickedTableId;

        let [error, message] = await getDestinationRobot(userId, destinationTable);
        if (!error) {
            console.log("[MapPresenter] getDestinationRobot success : ", message);
            this.setState(prevState => ({
                ...prevState,
                isModalOpen: false,
                clickedTableId: 0
            }))
        }
        else {
            console.log("[MapPresenter] getDestinationRobot fail  : ", message);
            this.setState(prevState => ({
                ...prevState,
                isModalOpen: false
            }))
        }
    }
    
    async clickInitModalPermit(userId){
        const clickedRobotId = this.state.clickedRobotId;

        let [error, message] = await getInitOneRobot(userId, clickedRobotId);
        if (!error) {
            console.log("[MapPresenter] getInitOneRobot success : ", message);
            this.setState(prevState => ({
                ...prevState,
                isModalOpen: false,
                clickedRobotId: 0
            }))
        }
        else {
            console.log("[MapPresenter] getInitOneRobot fail  : ", message);
            this.setState(prevState => ({
                ...prevState,
                isModalOpen: false,
                clickedRobotId: 0
            }))
        }
    }
    async clickReturnModalPermit(userId){
        const clickedRobotId = this.state.clickedRobotId;
        let [error, message] = await getHomeRobot(userId, clickedRobotId);
        if (!error) {
            console.log("[MapPresenter] getHomeRobot success : ", message);
            this.setState(prevState => ({
                ...prevState,
                isModalOpen: false,
                clickedRobotId: 0
            }))
        }
        else {
            console.log("[MapPresenter] getHomeRobot fail  : ", message);
            this.setState(prevState => ({
                ...prevState,
                isModalOpen: false
            }))
        }
    }

    render() {
        
        
        const userId = sessionStorage.getItem('userId');
    const mapId = sessionStorage.getItem('mapId');

        const isModalOpen = this.state.isModalOpen;
        const robotStatus = this.state.robotStatus;
        const mapBackgroundSrc = "assets/map-background/map-background-"+mapId+".png";

        return (
            <div className="content" id="map">
                <div className="content-title text-color-1"> <GrMap className="icon" />서빙 모드</div>

                <div className="grid-container">

                    <div className="grid-item item-scroll" id="item-map">
                        <div id="map-top">
                            <div id="discription">
                                <MdErrorOutline className="icon icon-w" />
                                <p>음식을 보낼 <span className="text-color-3">테이블을 선택</span>하거나 정보를 볼 <span className="text-color-3">로봇을 선택</span>하세요</p>
                            </div>

                        </div>
                        {/* ref={this.mapContainer}  */}
                        <div id="map-container">
                            <RobotPosition clickedRobotId={this.state.clickedRobotId} setRobotStatus={p => this.setRobotStatus(p)} onClick={i=>this.handelRobotClick(i)}/>
                            <TablePosition xytypeList={this.state.mapTable} onClick={i=>this.handelTableClick(i)}/>
                            <img src={mapBackgroundSrc} alt="map-background-img" className="map-background-img"  onClick={()=>this.handelNotRobotClick()}/>
                        </div>

                    </div>
                    
                    <div className="grid-item" id="item-serving">
                        <div className="grid-title text-color-1">로봇 서빙 현황</div>
                            <ul>
                                {robotStatus.map(robot => (
                                    <RobotServing robot={robot} key={robot.robot_id}  onClick={()=>this.handelRobotClick(robot.robot_id)} clickedRobotId={this.state.clickedRobotId} />
                                ))}
                            </ul>
                            <div id="error-control">
                                <button id="return" className="text-color-2 background-color-w" onClick={this.handleReturnClick}>복귀</button>
                                <button id="init" className="text-color-w background-color-1" onClick={this.handleInitClick}>초기화</button>
                            </div>
                    </div>
                    
                </div>
                <Modal title={this.state.modalTitle} content={this.state.modalContent} open={isModalOpen} onCancel={() => this.setState(prevState => ({ isModalOpen: false }))}
                    onPermit={isModalOpen == "table" ? () => this.clickTableModalPermit(userId)
                            : isModalOpen == "return" ? () => this.clickReturnModalPermit(userId)
                            : isModalOpen == "init" ? () => this.clickInitModalPermit(userId)
                                : () => this.setState(prevState => ({ isModalOpen: false }))}>
                </Modal>
            </div>
        )
    }
}

export default MapPresenter
