import React, { useState, useEffect } from 'react'

// component
import ErrorControlButton from './ErrorControlButton';
import RobotPosition from './RobotPosition';
import TablePosition from './TablePosition';
import RobotServing from './RobotServing';
// function
import { getMapTable } from './MapController';
// icon
import { FaHandPointer, FaMapMarkerAlt } from 'react-icons/fa';
import {HiClipboardList} from 'react-icons/hi';
// http
import axios from 'axios';
axios.defaults.baseURL = process.env.REACT_APP_SERVER_URL;    //15.165.182.10
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';


function ServingPresenter() {

    const [clickedRobotId, setClickedRobotId] = useState(1);    //클릭된 로봇
    const [robotData, setRobotData] = useState([]); //불러온 로봇 데이터
    const [tableData, setTableData] = useState([]); //불러온 테이블 데이터

    const mapId = sessionStorage.getItem('mapId');
    const mapBackgroundSrc = "assets/map-background/map-background-" + mapId + ".png";  //배경 이미지

    function getRobotData() {   // 로봇 데이터 불러오기
        const userId = sessionStorage.getItem('userId');

        var interval = setInterval(() => {
            console.log("[RobotPosition] get robot position")
            axios.get('/controller/robot/position/' + mapId)    /*api이름 data로 변경해야함 upgradable*/
                .then((response) => {
                    setRobotData(response.data.robot_position.filter(robot => robot.used == userId)); // 불러온 데이터중에서 user의 음식이 올려진 로봇만 필터링
                })
                .catch((error) => {
                    console.log("call robot positions item fail.");
                    return interval;
                })
        }, 1000);   //1초마다 반복
        return interval
    }
    async function getTableData() {  //table 위치 불러오기
        let [error, result] = await getMapTable();
        if (error) {
            alert("[MapPresenter] getMapTable 불러오기 실패 : ", error);
            return;
        }
        setTableData(result);
        // console.log(result);
    }


    useEffect(() => {   // 로봇 데이터 불러오기 반복
        const interval = getRobotData();
        return () => clearInterval(interval);   // 다른 창으로 넘어갈때 로봇 불러오기 멈춤
    }, [])
    /* [robotPositionList] 쓰는 이유. (근데 쓰면 계속 불러오드람. 더 찾아보기.)
    근데 여기서 새로고침을 하지 않으면 등록된 모습을 확인할 수 없다. 그 이유는 useEffect 함수의 두번째 인자로 빈 배열을 주면 처음 사이트에 들어갔을 1회만 렌더링이 되기 때문에 다시 값을 불러오지 않는다.
    그래서 다음과 같이 수정해 주면 등록이 완료될 때 게시판이 업데이트 되는 모습을 확인할 수 있다.*/
    useEffect(() => {   // 테이블 데이터 불러오기 
        console.log("rendering~");
        getTableData();
    }, []);    //처음 랜더링에서만

    function handelNotRobotClick() {     // 배경 이미지를 클릭할때 클릭된 로봇값 초기화
        setClickedRobotId(0);
    }

    return (
        <div>
            <div className="content" id="map">


                <div className="content-title"> <FaMapMarkerAlt className="icon" />서빙 모드</div>

                <div className="grid-container">
                    <div className="grid-item item-scroll" id="item-map">
                        <div id="map-top">
                            <div id="discription" >

                                <p className="text-color-1">
                                    <span className="icon-div background-color-2"><FaHandPointer className="icon-w" /></span>
                                    음식을 보낼 <span className="text-color-2">테이블을 선택</span>하거나 정보를 볼 <span className="text-color-2">로봇을 선택</span>하세요</p>
                            </div>

                        </div>
                        <div id="map-container">
                            <RobotPosition clickedRobotId={clickedRobotId} robotData={robotData} setClickedRobotId={setClickedRobotId} pageType={"serving"}/>
                            <TablePosition tableData={tableData} />
                            <img src={mapBackgroundSrc} alt="map-background-img" className="map-background-img" onClick={handelNotRobotClick} />
                        </div>

                    </div>
                    <div className="grid-item" id="item-serving">
                        <div className="grid-title text-color-1"><HiClipboardList className="icon icon-2"/>로봇 서빙 현황</div>
                        <ul>
                            {robotData.map(robot => (   /*upgradable 하나의 robotData의 의미로 robot말고 다른 변수명 찾기 */
                                <RobotServing robot={robot} key={robot.robot_id} clickedRobotId={clickedRobotId} setClickedRobotId={setClickedRobotId} />
                            ))}
                        </ul>
                        <ErrorControlButton clickedRobotId={clickedRobotId} setClickedRobotId={setClickedRobotId} />
                    </div>
                </div>


            </div>

        </div>
    )
}

export default ServingPresenter
