import React from 'react'

function RobotInfo({robot, clickedRobotId}) {
    if(robot == null) return null;
    else if (robot.robot_id != clickedRobotId) return null;
    else {
        const robotImgSrc = "/assets/robot/basic_robot.png";  //로봇 이미지    
        var destination = "";
        var tableName = robot.table_name;
        if(tableName == null){
            destination = "현재 위치";
        }
        else if (isNaN(tableName)){  // table name 이 숫자가 아닐 때 (ex. 배달)
            //console.log("not number!");
            destination = tableName+"구역";
        }
        else{
            destination = "테이블 "+tableName+"번";
        }

        return (

            <div id="robot-info">
                <div id="robot-basic-img-div">
                <img src={robotImgSrc} alt="robot-basic-img" className="robot-basic-img" />
                </div>
                <div id="info-text">
                    <div className="text-color-1">로봇 ID : <span className="text-color-3">{robot.robot_id}</span></div>
                    <div className="text-color-1">목적지 : <span className="text-color-3">{destination}</span></div>
                    <div className="text-color-1">베터리 : {robot.battery}</div>
                </div>
            </div>
        )

}

}

export default RobotInfo
