import React from 'react';
import {createPortal} from 'react-dom';
// icon
import { AiOutlineInfoCircle } from 'react-icons/ai';


function Modal({ title,highlight, content, open, onPermit, onCancel }) {
  if (!open) return null;
  return createPortal(
    <div id="modal">
      <div id="overlay" />
      <div id="modal-box" className="background-color-w text-color-1">
        <h3 id="modal-title" className="text-color-1"><AiOutlineInfoCircle className="icon icon-2"/>{title}</h3>
        <div id="modal-content" className="text-color-1"><span className="text-color-2">{highlight}</span>{content}</div>
          <button id="cancle-button" className="text-color-1 background-color-w" onClick={onCancel}>아니오</button>
          <button id="ok-button" className="text-color-w background-color-2" onClick={onPermit}>예</button>
      </div>
    </div>,
    document.getElementById('modal')

  )
}

export default Modal
