import React, {useState, useEffect} from 'react'
import {postControllerLogin} from './LoginController';
import { useCookies } from 'react-cookie';  // 쿠키, 아이디 기억하기
import './style.css';

// 아이콘   
import {BiLogIn} from 'react-icons/bi'
import {IoIosEye, IoIosEyeOff} from 'react-icons/io'
import {AiOutlineLock, AiOutlineUser} from 'react-icons/ai'



function LoginPresenter() {
    
    // 아이디, 패스워드 입력값 변수. 초기는 null
    const [inputId, setInputId] = useState("");
    const [inputPassword, setInputPassword] = useState("");

    const [showPassword, setshowPassword] = useState("");   //password 보여줌 여부, 보여준다면 input type:text 아니면 password
    const [isRemember, setIsRemember] = useState(false);    // id 기억 여부 
    const [cookies, setCookie, removeCookie] = useCookies(['rememberId']);  // 쿠키 접근, 기억된 아이디는 rememberId에 저장

    useEffect(() => {   // 처음 열렸을때 기억된 id가 있는지 확인
        if (cookies.rememberId !== undefined) {
            setInputId(cookies.rememberId);
            setIsRemember(true);
        }
    }, []);
    
    // input에 입력시 변수 갱신
    const handleInputId = (e) => {
        setInputId(e.target.value)
    }
    const handleInputPassword = (e) => {
        setInputPassword(e.target.value)
    }
    // password show 아이콘 눌렀을 때
    const handleClickShowPassword = () => {
        setshowPassword(!showPassword);
    };
    const handleMouseDownPassword = (event) => {
        event.preventDefault();
    };
    // remember 체크박스 눌렀을 때
    const handleRemember = (e) => {
        setIsRemember(e.target.checked);
    }
    const onSubmit = async () => {
        //아이디 기억 여부
        if (isRemember) {
            setCookie('rememberId', inputId, { maxAge: 2000 });
        } else {
            removeCookie('rememberId');
        }
        // 서버로 로그인 정보 확인
        let [valid, result] = await postControllerLogin(inputId, inputPassword);
        if (valid) {
            console.log("[LoginPresenter] login success : ");
            console.log(result.user_name);
            sessionStorage.setItem('userId', inputId)
            sessionStorage.setItem('mapId', result.map_id)
            sessionStorage.setItem('userName', result.user_name)
            sessionStorage.setItem('logoSrc', result.logo_src)
            document.location.href = '/'
        }
        else {
            console.log("[LoginPresenter] login failed, error  : ", result);
            alert(result);
            return;

        }
        

    }
    return (
        <div className="content" id="login">
            <div id="top">
                <img className='login-logo-img' id="logo" src={"/assets/hprobot-logo/hprobot-logo-white-2line.png"} alt="login-logo-img" />
                <div id="title-div">
                    <a id="title">헬퍼로보틱스 관제시스템</a>
                </div>
            </div>
            <div id="middle">

                <div id="login-icon-div">
                    <AiOutlineUser id="login-icon" />
                </div>
                <div className="login-div">
                    <div id="size">
                        <a className="title">Log in</a>

                        <div id="id-input-div" className="input-div">
                            <AiOutlineUser className="icon" />
                            <input id="id-input" className="input" type="text" name="inputId" placeholder="아이디" value={inputId} onChange={handleInputId} />
                        </div>
                        {/* <div class="form_text_alert_padding">
                        <div id="alert_username" class="form_text_alert"></div>
                    </div> */}
                        <div id="password-input-div" className="input-div">
                            <AiOutlineLock className="icon" />
                            <input id="password-input" className="input" name="inputPassword" placeholder="패스워드"
                                type={showPassword ? 'text' : 'password'} value={inputPassword} onChange={handleInputPassword} />
                            <div id="show-icon-div" onClick={handleClickShowPassword} onMouseDown={handleMouseDownPassword}>
                                {showPassword ? <IoIosEyeOff id="show-icon" /> : <IoIosEye id="show-icon" />} </div>
                        </div>

                        <div id="remember">
                            <input id="checkbox" type="checkbox" onChange={handleRemember} checked={isRemember} />
                            <label for="checkbox"></label>
                            <span id="text">아이디 저장</span>
                        </div>
                        <div id="login-button-div">
                            <button id="login-button" onClick={onSubmit}>로그인</button>
                        </div>
                        <div id="forgot">Forgot Password ? </div>
                    <img className='login-logo-img' id="logo" src={"/assets/hprobot-logo/hprobot-logo-1line.png"} alt="login-logo-img" />

                    </div>
                </div>
            </div>

        </div>

    )
}

export default LoginPresenter
