

import React, {useState} from 'react'
import Modal from './../Modal/Modal';
import {getMapTable, getInitOneRobot, getDestinationRobot, getHomeRobot} from './MapController';

// , setModal, setTitle, setContent
function ErrorControlButton({clickedRobotId,setClickedRobotId}) {
    
    const [modalTitle, setModalTitle] = useState("제목");
    const [modalContent, setModalContent] = useState("내용");
    const [modalHighlight, setModalHighlight] = useState("강조");
    const [isModalOpen, setIsModalOpen] = useState(false);
    
    function handleInitClick() {   // 로봇 초기화 버튼
        console.log("init one modal click!");

        if (clickedRobotId == 0){   // 로봇이 선택되지 않았을 때
            setIsModalOpen("message");
            setModalTitle("로봇 초기화");
            setModalHighlight("선택된 로봇");
            setModalContent("이 존재하지 않습니다.");
            return;
        }
        // 선택된 로봇이 존재 할 때
        setIsModalOpen("init");
        setModalTitle("로봇 초기화");
        setModalHighlight(clickedRobotId+"번 로봇");
        setModalContent("을 초기화하시겠습니까?");
        // nothing do
    }
    function handleReturnClick() {   // 로봇 복귀 버튼
        console.log("return one modal click!");

        if (clickedRobotId == 0){   // 로봇이 선택되지 않았을 때
            setIsModalOpen("message");
            setModalTitle("로봇 초기화");
            setModalHighlight("선택된 로봇");
            setModalContent("이 존재하지 않습니다.");
                        return;
        }
        // 선택된 로봇이 존재 할 때
        setIsModalOpen("return");
        setModalTitle("로봇 초기화");
        setModalHighlight(clickedRobotId+"번 로봇");
        setModalContent("을 복귀시키겠습니까?");

        // nothing do
    }
    async function clickInitModalPermit(){
        const userId = sessionStorage.getItem('userId');
        let [error, message] = await getInitOneRobot(userId, clickedRobotId);
        if (!error) {
            console.log("[MapPresenter] getInitOneRobot success : ", message);
        }
        else {
            alert("[MapPresenter] getInitOneRobot fail  : ", message);
        }
            setIsModalOpen(false);
            setClickedRobotId(0);

    }
    async function clickReturnModalPermit(){
        const userId = sessionStorage.getItem('userId');
        let [error, message] = await getHomeRobot(userId, clickedRobotId);
        if (!error) {
            console.log("[MapPresenter] getHomeRobot success : ", message);
        }
        else {
            alert("[MapPresenter] getHomeRobot fail  : ", message);
        }
        setIsModalOpen(false);
        setClickedRobotId(0);
    }

    return (
        <div id="error-control">
            <button id="return" className="text-color-3 background-color-w" onClick={handleReturnClick}>복귀</button>
            <button id="init" className="text-color-w background-color-3" onClick={handleInitClick}>초기화</button>
            <Modal title={modalTitle} highlight={modalHighlight} content={modalContent} open={isModalOpen} onCancel={()=>setIsModalOpen(false)}
                    onPermit={isModalOpen == "return" ? clickReturnModalPermit
                    : isModalOpen == "init" ? clickInitModalPermit
                        : ()=>setIsModalOpen(false)}>
                </Modal>
        </div>
    )
}

export default ErrorControlButton
