import React, {useState} from 'react'
import Modal from './../Modal/Modal';
import {getDestinationRobot} from './MapController';

function TableItem(props) {
    const tableName = props.data['table_name'];
    const posX = (props.data['x']) * 0.01 - 2.4;
    const posY = (props.data['y']) * 0.01 - 1.6;

    return (
        <div className="table-button background-color-3 " onClick={props.onClick} style={{left:posX+"rem", top:posY+"rem"}}>
            <div className="table-button-name text-color-w">{tableName}</div>
        </div>
    )

}

function TablePosition({tableData}) {
    const modalTitle = "서빙 시작 안내";
    const modalContent = "으로 서빙을 시작할까요?";
    
    // const [modalContent, setModalContent] = useState("테이블을 클릭하지 않았습니다.");
    const [modalHighlight, setModalHighlight] = useState("");

    const [isModalOpen, setIsModalOpen] = useState(false);
    const [clickedTableId, setClickedTableId] = useState(0);

    function handleTableClick(tableInfo) {   // 테이블 버튼을 눌렀을 때 모달을 띄우는 함수
        const  tableId = tableInfo[0];
        const  tableName = tableInfo[1];
        var highlightString = "";
        if (isNaN(tableName)){  // table name 이 숫자가 아닐 때 (ex. 배달)
            //console.log("not number!");
            highlightString = tableName+"구역";
        }
        else{
            highlightString = "테이블"+tableName+"번";
        }
        // 선택된 로봇이 존재 할 때
        setIsModalOpen(true);
        setModalHighlight(highlightString);
        setClickedTableId(tableId);
        // nothing do
        console.log("table modal click!");
    }
    
    async function clickTableModalPermit(){    //모달에서 확인을 눌렀을 때 동작
        const userId = sessionStorage.getItem('userId');

        let [error, message] = await getDestinationRobot(userId, clickedTableId);
        if (!error) {
            console.log("[TablePosition] getDestinationRobot success : ", message);
        }
        else {
            console.log("[TablePosition] getDestinationRobot fail  : ", message);
        }
        //모달 데이터 초기화
        setIsModalOpen(false);
        setModalHighlight("");
        setClickedTableId(0);

    }

    return (
        <div>
            {tableData.map(tableItem => (
                <TableItem data={tableItem} key={tableItem['table_id']} onClick={() => handleTableClick([tableItem['table_id'], tableItem['table_name']])}/>
            ))}
            <Modal title={modalTitle} highlight={modalHighlight}  content={modalContent} open={isModalOpen} onCancel={()=>setIsModalOpen(false)}
                    onPermit={clickTableModalPermit}>
                </Modal>
        </div>
    )
}

export default TablePosition
