import React from 'react'
import {BiCheckSquare} from 'react-icons/bi'
function RobotServing({robot, clickedRobotId, setClickedRobotId}) {
    var stateString = "이동중" 

    if (robot.state == 2) return null;  // 물건 없음
    else if(robot.current_node == robot.final_node) stateString="서빙완료"; //물건 올린 상태에서 목적지 도착
    const robotImgSrc = "/assets/robot/octa_robot_white.png";   //로봇 이미지
    // serving정보칸 배경 색상 (강조 여부)
    var statusColor = "#ffffff00";
    if(robot.robot_id == clickedRobotId){
        statusColor = "#0000ff11";
    }
    // 테이블 이름에 따른 목적지 문자열 구분(숫자 / 배달)
    var destination = "";
        var tableName = robot.table_name;
        if(tableName == null){
            destination = "현재 위치";
        }
        else if (isNaN(tableName)){  // table name 이 숫자가 아닐 때 (ex. 배달)
            //console.log("not number!");
            destination = tableName+"구역";
        }
        else{
            destination = "테이블 "+tableName+"번";
        }
    /*upgradable 함수의 위치? 함수형 컴포넌트에서... */
    function handelRobotClick(){
        // console.log("robot click!");
        setClickedRobotId(robot.robot_id);
    }

    return (
        <li id="robot-serving-item" onClick={handelRobotClick} style={{ backgroundColor:statusColor}}>
            <img src={robotImgSrc} alt="robot-icon-img" className='robot-icon-img float' />
                <div id = "serving-info" className="float">

                    <span id="arrive-robot-string" className="text-color-1">로봇</span>
                    <span id="arrive-robot" className="text-color-1" >{robot.robot_id}</span>

                </div>
                <div id = "serving-info" className="float">
                <span id="arrive-node" className="text-color-2">{destination}</span>
                <span id="arrive-robot-string" className="text-color-1"> {stateString}</span>
                {stateString == "이동중" ?
                    <div id="arrow-div">
                        <div id="arrow-right-div">
                        <p id="arrow-right"></p>
                        <p id="arrow-right"></p>
                        </div>
                    </div>
                    : <BiCheckSquare className="icon icon-2"/>
                }
            </div>
        </li>
    )
}

export default RobotServing
