
import React, {useState, useEffect} from 'react'
import axios from 'axios';
axios.defaults.baseURL = process.env.REACT_APP_SERVER_URL;    //15.165.182.10
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'

// axios.defaults.baseURL = 'http://localhost:8080/server/src/public';    //15.165.182.10
// axios.defaults.baseURL = 'http://15.165.182.103:8080/server/src/public';     //15.165.182.103
// axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;

function RobotItem(props) {
    var robotImgSrc = "/assets/robot/octa_robot_white.png"; // 로봇 이미지 (색상과 번짐이 다름)
    if(props.robotId == props.clickedRobotId){
        robotImgSrc = "/assets/robot/octa_robot_yellow.png";       // 클릭된 로봇이라면 노랑
    }
    else if(props.pageType == "monitoring" && props.state == "1"){  // 모니터링 페이지에서 이동중이라면 초록
        robotImgSrc = "/assets/robot/octa_robot_green.png";
    }
    const posX = (props.posX) * 0.01-0.5; //-0.5    // db에 저장된 xy위치는 1rem의 100배의 수치이다.
    const posY = (props.posY) * 0.01-0.5;   //중앙에 위치하기 위해 0.5전부터 그림. 이건 상수가 아닌 변수로 /*upgradeable*/
    

    function handelRobotClick(){
        // console.log("robot click!");
        props.setClickedRobotId(props.robotId);
    }
    return (
        <div className="robot"  onClick={handelRobotClick} style={{ left: posX+"rem", top: posY+"rem"}}>
            <img src={robotImgSrc} alt="robot-icon-img" className='robot-icon-img'/>
        </div>
    )

}
function calcAngleDegrees(x, y) {   // 하나의 좌표와 원점사이의 arctan(degree각도) 계산
    return Math.atan2(y, x) * 180 / Math.PI;
}

function TargetItem(props){

    const ax = (props.currX-(-0.5)) * 0.01;
    const ay = (props.currY-(-0.5)) * 0.01;
    const bx = (props.finalX-(-0.5)) * 0.01;
    const by = (props.finalY-(-0.5)) * 0.01;
    // 선분의 중심은 두 점사이의 중심
    const centerX = (ax+bx)/2;
    const centerY = (ay+by)/2;

    // 선분의 길이는 두 점 사이의 길이 (width)
    const size = Math.sqrt(Math.pow(ax-bx, 2) + Math.pow(ay-by,2));

    // target -형태의 div 시작위치는 중점에서 길이의 반절만큼 왼쪽으로 가야함
    const top = centerY;
    const left = centerX - (size)/2;

    // 수평선으로부터 회전된 각도는 한 점이 원점이라고 할 때 arctan 값
    const degree = calcAngleDegrees(ax-bx, ay-by);
    const transformString = "rotate("+degree.toString() + "deg)"
    return (
        <div className="target" style={{ left: left+"rem", top: top+"rem", width: size+"rem", transform:transformString}}>
        </div>
    )
}

function RobotPosition({ clickedRobotId, robotData, setClickedRobotId, pageType }) {
    function test() {
        console.log("test");
        return;
    }
    if (robotData.length > 0 || pageType=="serving") {
        return (
            <div>
                {robotData.map(robot => (

                    <RobotItem
                        key={robot.robot_id} clickedRobotId={clickedRobotId} setClickedRobotId={setClickedRobotId} pageType={pageType}
                        robotId={robot.robot_id} state={robot.state} posX={robot.curr_x} posY={robot.curr_y} />
                ))}
                {robotData.filter(robot => robot.state == 1).map(frobot => (
                    <TargetItem
                        currX={frobot.curr_x} currY={frobot.curr_y} finalX={frobot.final_x} finalY={frobot.final_y}
                        key={frobot.robot_id} />
                ))}

            </div>
        )
    } else {  // 조회 데이터 존재하지 않을 경우
        console.log(" robot data를 불러오는데 실패했습니다.");
        return (
            <div>
                <button className="data-reload" onClick={test}>데이터 불러오기</button>
            </div>
        )
    }


}


export default RobotPosition
